<template>
	<view>
		<ReturnHead title="头像" complete=""></ReturnHead>
		<text class="iconfont icon-caidan caidan-icon" @tap="popmunu"></text>
		<view class="back-ground">
			<!-- <text style="color: white;">{{itemList}}</text> -->
			<view>
				<!-- <image :src="userpic" class="user-head"></image> -->
				<!-- <image src="../../../UI/profile.jpg"></image> -->
				<image v-for="item in imgArr" :src="item" class="user-head" @tap="previewImage"></image>				
			</view>
		</view>
	</view>
</template>

<script>
	import ReturnHead from '../../../components/message/ReturnHead.vue'
	import{
		postUserPhoto,
	}from "../../../axios/message/message.js";
	export default {
		onLoad:function(e){
			this.imgArr[0]=e.head; 
			// this.getList();
			// console.log(this.userpic);
			  },
		data() {
			return {
				// userpic:"",
				imgArr:[],
				img:[],
				index:0,
				// itemList:[]
			}
		},
		
		components: {
			  ReturnHead
		},
		methods:{
			chooseImg(){
				var that=this;
				uni.chooseImage({
					count:1,
					sizeType:["original","compressed"],
					sourceType:['camera','album'],
					success:(res)=> {
						that.imgArr=res.tempFilePaths;	
						that.img=res.tempFiles;
						that.getUploadFile();
					}
				});
			},
			getUploadFile(){
				let files=[];
				for(let i=0;i<this.img.length;i++){
					files[i]={
						name:"photo",
						file:this.img[i],
						uri:this.imgArr[i]
					}
				}
				// console.log(files);
				// console.log("ok");
				this.postUserPhoto(files);
			},
			postUserPhoto(files){
				return postUserPhoto(files).then(
				(res) => {
					// this.itemList=res.data;
			        var obj = JSON.parse(res.data);
					console.log(obj.state);
					if(obj.state==200){
						 setTimeout(function(){
						 uni.showToast({
						 	title: '发布成功',
						 	icon:'none',
						 	duration: 2000
						 })
						 },500);
					}
					},
				(err) => {
				  console.log(err);
				}
				);
			},
			//预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imgArr
				})
			}, 
			saveImg(){
				// console.log(this.imgArr[0]);
				uni.saveImageToPhotosAlbum({//保存图片到系统相册。			
					filePath: this.imgArr[0],//图片文件路径
					success: function() {
						uni.showToast({
						title: '图片保存成功',
						icon: 'none',
								});
							},
					fail: function(e) {
						console.log(e);
						uni.showToast({
							title: '图片保存失败',
							icon: 'none',
								});
							}
					   });
				    },					
			popmunu(){
				var that=this;
				// this.chooseImg();
				uni.showActionSheet({
				    itemList: ['从手机相册中选择', '保存到手机'],
					popover:{						
					},
				    success: function (res) {
				        if(res.tapIndex===0){
							// var that=this;
							that.chooseImg();
							// that.getUploadFile();
							}
						if(res.tapIndex===1){
							that.saveImg();
						}
						// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
				    },
				    fail: function (res) {
				        console.log(res.errMsg);
				    }
				});
			}
		}
		};
</script>

<style scoped>
@import url("../../../../static/icons/iconfont/iconfont.css");
.back-ground{
	background-color: #333333;
	width: 100%;
	height: 100vh;
	margin-top: 38rpx;
}
.caidan-icon{
	position: absolute;
	top: 100rpx;
	right: 42rpx;
	font-size: 43rpx;
}
.user-head{
	position: relative;
	width: 100%;
	height: 750rpx;
	top: 200rpx;
}
</style>
